	module.exports = {"data":{"markdownRemark":{"html":"<p>そろそろまた悪さをしでかし始めるかも。詳細はもう少し形になってから(4月頃？)とさせていただきますが、今回はフリーウェアの東方二次創作になる予定です。</p>\n<h2>(拍)ようつべにもあげれ</h2>\n<p>めどい</p>\n<h2>(拍)ニコニコ見て来てみたら、意外といい人で実は叩きが自演なのではないかと～</h2>\n<p>残念ながら自演じゃないですね。全部ちゃんと届いた拍手です。あぁ、叩いている方が自演ってことなら分からなくもないかも。</p>\n<h2>(拍)新房以外のアニメも見ろよ</h2>\n<p>シャナとハルヒとらきすたくらいなら。</p>\n<h2>(拍)これはいいアホの子</h2>\n<p>いいえ、<a href=\"/image/illustrations/miku/3939.png\">バカの子です</a>。</p>\n<h2>(拍)今度はゾンビミクですか？</h2>\n<p>いいえ、充電中ミクです。</p>\n<h2>(拍)飢え死にするまでコピ本完成を待ってなくちゃならんのかね</h2>\n<p>いいえ、閉店は6時です。</p>\n<h2>(拍)本家でやらないようなって宣伝文句はいかがなもんかと</h2>\n<p>職人たるやモノで語れ、ですな。時封城のオビは「神主がやらぬなら俺がやる m9(^Д^ )」だったからなぁ。</p>\n<h2>(拍)＼みっくみく／</h2>\n<p>＼みっくみく／</p>\n<h2>(拍)ニコニコ動画なんかで油売ってないで落としたもの完成させてください</h2>\n<p>ごめんなさい、カメのペースですがゆっくり進めてはいます。とりあえずオヤスミモードなので勘弁してください orz</p>\n<h2>(拍)初音MiQキモいよ</h2>\n<p>なんと言おうと<a href=\"/haznemic\">haznemic</a>。</p>","frontmatter":{"cover":null,"date":"2008-02-16T00:00:00+09:00","strDate":"2008/2/15","title":"休止宣言してからまだ間もないですが"}}},"pathContext":{"path":"/ja/blog/2008/02/16-休止宣言してからまだ間もないですが/","langKey":"ja"}}

